<script>
import ProductDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import ModelPriceBook from "../../../components/Common/Modal/ModelPriceBook.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
import $ from "jquery";
export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        ModelPriceBook,
        modalattachment,
        crmreadmore
    },
    data() {
        return {
            activeKey: 1,
            isNotes: false,
            isAttachment: false,
            isPriceBook: false,
            iscontactDetails: null,
            LeftViewData: [],
            RightViewData: null,
            showAttachmentModal: false,
            attachmentData: null,
            activitiesData: [],
            productData: null,
            noteData: [],
            productHeaders: null,
            contactsData: null,
            contactsHeader: null,
            timelinedata: [],
            Headers: [],
            priceData: [],
            noRecordForTab: false,
            isShowNoteDialogue: null,
            showPriceBookModal: null,
            PriceBookData: null,
            imagePath: '',
            itemName: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            StatusListForListing: [],
            PageSize: 50,
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize: "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes: "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
            NoteListType: 'list',
            userid: null,
            companyId: null,
            isShowNotePopup: false,
            containerName: null,
            isLoading:false
        }
    },
    created: function () {
        this.userid = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containerName = this.GetUserInfo.ContainerName;
        var VueObj = this;
        VueObj.getStatuslistForListing("CRM", "en");
        VueObj.FetchData()
    },
    updated: function () {
        var vm = this;
        setTimeout(function () {
            vm.setHeaderForSelectedTab();
        }, 500);
        $("#ancActivityExpandAllCollapseAll").click(function () {
            if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
        });
    },
    methods: {
        openPriceBookModel(priceBookDetails) {
            this.iscontactDetails = priceBookDetails
            this.showPriceBookModal = (this.showPriceBookModal) ? false : true;
        },
        openShowNotePopup: function () {
            var ths = this;
            ths.isShowNotePopup = true;
            $("#tab-notes").trigger("click");
            ths.loadData("notes");
        },
        openCRMAttachmentModal: function () {
            var VueObj = this;
            VueObj.showAttachmentModal = (VueObj.showAttachmentModal) ? false : true;
        },
        DisableButton: function () {
            var vm = this;
            vm.isAttachment = false;
            vm.isNotes = false;
            vm.isPriceBook = false;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            ProductDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        FetchData: function () {
            var VueObj = this
            VueObj.isLoading=true;
            ProductDataService.getQuickViewDetails(VueObj.ApiURL).then(function (response) {
                VueObj.isLoading=false;
                var fullLeftJSON = response.data.LEFTDATA;
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    return element.form_field_visibility == 'YES';
                });
                VueObj.imagePath = response.data.IMAGEPATH;
                if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                    var fullJSON = response.data.RIGHTDATA;
                    var gruopBy = "";
                    $.each(fullJSON, $.proxy(function (index, item) {
                        gruopBy = item.created_date;
                        var chkGroup = $.grep(VueObj.timelinedata, function (element, index) {
                            return element.groupKey == gruopBy;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullJSON, function (element, index) {
                                return element.created_date == gruopBy;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    groupKey: gruopBy,
                                    groupData: returnedData
                                };
                                VueObj.timelinedata.push(objGroup);
                            }
                        }
                    }, VueObj));

                    VueObj.noRecordForTab = false;
                } else {
                    VueObj.noRecordForTab = true;
                }
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;
                    $.each(fullLeftJSON, $.proxy(function (index, item) {
                        if (item.name == 'name') {
                            VueObj.itemName = item.value;
                        }
                        var chkGroup = $.grep(VueObj.LeftViewData, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup != null && chkGroup.length == 0) {
                            var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (returnedData != null && returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    LeftViewData: returnedData
                                };
                                VueObj.LeftViewData.push(objGroup);
                            }
                        }
                        groupId = item.group_id;
                    }, VueObj));
                }
              
                setTimeout(function () { VueObj.CheckBoxBootstrap() }, 500);
            });
        },
        loadData: function (tabName) {
            var VueObj = this;
            VueObj.DisableButton();
            if (tabName == "notes") {
                VueObj.activeKey = 2;
                VueObj.isNotes = true;
            }
            else if (tabName == "attachments") {
                VueObj.activeKey = 3;
                VueObj.isAttachment = true;
            }
            else if (tabName == "price") {
                VueObj.activeKey = 4;
                VueObj.isPriceBook = true;
            }
            if (tabName != "" && tabName != null) {
                if (tabName == "attachments") {
                    ProductDataService.AttachmentListing(VueObj.ApiURL).then(function (response) {
                        if (response.data != null) {
                            VueObj.attachmentData = response.data.data;
                            VueObj.noRecordForTab = false;
                        } else {
                            VueObj.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "activities") {                  
                    ProductDataService.ActivityListing(VueObj.ApiURL).then(function (response) {
                        if (response.data != null) {
                            VueObj.activitiesData = response.data.data;
                            VueObj.noRecordForTab = false;
                        } else {
                            VueObj.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "price") {               
                    ProductDataService.PriceBookListing2(VueObj.ApiURL+'&sortBy='+ VueObj.SortBy +"&sortExp=" + VueObj.SortExp + "&pageSize=" + VueObj.PageSize + "&pageNumber=" + VueObj.PageNumber).then(function (response) {
                        if (response.data != null) {
                            VueObj.Headers = response.data.schema;
                            if (response.data.data != null && response.data.data.length > 0) {
                                VueObj.priceData = response.data.data;
                                VueObj.noRecordForTab = false;
                            } else {
                                VueObj.priceData = [];
                                VueObj.noRecordForTab = true;
                            }
                        } else {
                            VueObj.priceData = [];
                            VueObj.Headers = [];
                            VueObj.noRecordForTab = true;
                        }
                        setTimeout(function () { VueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                    });
                }
                else if (tabName == "timeline") {
                    VueObj.activeKey = 1;
                    ProductDataService.TimelineListing(VueObj.ApiURL).then(function (response) {
                        if (response.data != null) {
                            VueObj.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(fullJSON, $.proxy(function (index, item) {
                                gruopBy = VueObj.$options.filters.formatDate(item.Complete_datetime);
                                var chkGroup = $.grep(VueObj.timelinedata, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(fullJSON, function (element, index) {
                                        return VueObj.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData
                                        };
                                        VueObj.timelinedata.push(objGroup);
                                    }
                                }
                            }, VueObj));
                            VueObj.noRecordForTab = false;
                        } else {
                            VueObj.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "notes") {
                    VueObj.noteData = [];
                    ProductDataService.NoteListing(VueObj.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            VueObj.noteData = response.data.data;
                            VueObj.noRecordForTab = false;
                            setTimeout(function () { VueObj.ResponsiveDataTable('tab-notes-content') }, 500);
                        } else {
                            VueObj.noRecordForTab = true;
                        }
                    });
                }
            }
        },
        deassociatePricebook(value) {
            var vm = this;
            if (value > 0) {
                vm.confirmR(vm.$t("ConfirmDeAssociatePricebook"), true, null, function () {

                    var postJSON = {
                        ids: value,
                      
                        moduleName: "CRM",
                        subModuleCode: vm.$route.params.subModuleCode,
                        refCode: 'DELETE_VIEW_PRICEBOOK',
                        refId: vm.$route.params.id
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    ProductDataService.deleteActivityData(objectPostString).then(function (response) {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.loadData('price');
                            box = vm.ShowAlert(vm.$t('Product has been successfully De-associated'),"success",true, vm.$t('PriceBook'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('ErrorDeassociateFailure'), vm.$t('PriceBook'));
                        }
                       
                    });

                });

            }
            else {
                vm.ShowAlert(vm.$t("DeletedError"), vm.$t("PriceBook"));
            }
        },
    }
}
</script>


<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <div class="left-menu-tab div-click-event">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white textelipsis">{{ itemName }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu"  :class="{ 'active': activeKey == 1 }" href="javascript:void(0)"
                            data-bs-toggle="#tab-detail-content" role="tab" id="tab-Details"
                            v-on:click="loadData('timeline'); DisableButton()">
                            <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>{{ $t('Detail') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active': activeKey == 2 }" href="javascript:void(0)"
                            data-bs-toggle="tab" role="tab" data-bs-target="#tab-notes-content" id="tab-notes"
                            v-on:click="loadData('notes')">
                            <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>{{ $t('NOTE') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active': activeKey == 3 }" href="javascript:void(0)"
                            data-bs-toggle="tab" role="tab" data-bs-target="#tab-attachment-content" id="tab-attachment"
                            v-on:click="loadData('attachments')">
                            <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>{{ $t('Attachments') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active': activeKey == 4 }" href="javascript:void(0)"
                            data-bs-toggle="tab" role="tab" data-bs-target="#tab-price-content" id="tab-priceBook"
                            v-on:click="loadData('price')">
                            <em class="fa fa-money" aria-hidden="true" aria-selected="false"></em>{{ $t('PRICE_BOOKS') }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div class="tab-pane mb-2">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-name text-white" id="span_ForSelectedTabName"></span>
                        <span class="p-actions float-right">
                          
                            <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }"
                                title="" data-toggle-tooltip="tooltip" data-original-title="Card View"
                                v-on:click="NoteListType = 'card'" v-if="isNotes && NoteListType == 'list'">
                                <em class="fa fa-th-large"></em><br />{{ $t('CardView') }}
                            </a>
                            <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }"
                                title="" data-toggle-tooltip="tooltip" data-original-title="List View"
                                v-on:click="NoteListType = 'list'" v-if="isNotes && NoteListType != 'list'">
                                <em class="fa fa-list"></em><br />{{ $t('ListView') }}</a>
                            <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }"
                                title="" id="ancaddnotesloaddata" data-toggle-tooltip="tooltip"
                                data-original-title="Add Notes" v-on:click="openShowNotePopup()" v-if="isNotes">
                                <em class="fa fa-plus pr-2"></em><br />{{ $t('AddNotes') }}</a>
                            <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true }" 
                                v-on:click="openCRMAttachmentModal(null);" v-if="isAttachment">
                                <em class="fa fa-plus pr-2"></em><br />{{ $t('Add') }} {{ $t('Attachment') }}
                            </a>
                            <a href="javascript:void(0)" class="p-action-btn text-white transfer" title="" id=""
                                v-on:click="openPriceBookModel(null)" data-original-title="Associate Price Book"
                                v-if="isPriceBook">
                                <em class="fa fa-plus pr-2"></em><br />{{ $t('Associate') }} {{ $t('PriceBook') }}
                            </a>
                            <RouterLink to="/CRM/ItemListing" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em><br />{{ $t('BacktoList') }}
                            </RouterLink>
                            <RouterLink id="updateproduct" :to="'/CRM/ManageProductEdit/'+$route.params.id+'/0'" class="p-action-btn text-white product_a">
                                <em class="fa fa-pencil"></em><br />{{ $t('Edit') }}
                            </RouterLink>                            
                        </span>
                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                        </span>
                    </div>
                </div>
                <div class="tab-pane mb-0">
                    <div class="tab-content fsmtabcontrolMobileView listing" id="myTabContent">
                        <div class="expand-list">
                            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                            </a>
                        </div>
                        <div class="tab-pane p-0 pb-2 border-0" role="tabpanel" id="tab-detail-content"
                            aria-labelledby="tab-detail-content" :class="{ 'active show': activeKey == 1 }">
                            <div class="listing">
                                <div class="row">
                                    <div class="col-md-12">
                                        <div class="card mb-3 probx">
                                            <div class="card-header">
                                                <h2>{{ $t('Details') }}</h2>
                                                <div class="cont_right ">
                                                    <div class="bar">
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card-body">
                                                <div class="panel1" v-for="(group, index) in LeftViewData" :key="index">
                                                    <div class="heading-border">
                                                        <h5 class="h5 mb-2">
                                                            <span>{{$t(group.group_name, '') }}</span>
                                                        </h5>
                                                    </div>
                                                    <div class="row">
                                                        <div class="col-md-6 col-lg-3"
                                                            v-for="(item, index) in group.LeftViewData" :key="index">
                                                            <div class="form-group">
                                                                <label>
                                                                    <strong>{{ $t(item.name,
                                                                            item.display_name)
                                                                    }}:</strong>
                                                                    <span class="answer rightlabeltext ml-1"
                                                                        v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">{{ item.value | formatDate }}</span>
                                                                    <span class="answer rightlabeltext ml-1"
                                                                        v-if="item.dt_code == 'decimal'">{{ isBlank(item.value) ? "0.00" : item.value | currency
                                                                            
                                                                        }}</span>
                                                                    <span class="answer rightlabeltext ml-1"
                                                                        v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options != 'Lookup' && ((item.name != 'description' && item.name != 'term_and_conditions') || ((item.name == 'description' && item.name == 'term_and_conditions') && isBlank(item.value)))">{{
                                                                                isBlank(item.value) ? "--" : (item.value)
                                                                        }}</span>
                                                                    <crmreadmore
                                                                        v-if="(item.name == 'description' || item.name == 'term_and_conditions') && !isBlank(item.value)"
                                                                        v-bind:data="item.value"
                                                                        :heading="$t('Description')" length="100" class="ml-1">
                                                                    </crmreadmore>
                                                                    <span class="answer rightlabeltext ml-1"
                                                                        v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{
                                                                                isBlank(item.value) ? "--" : (item.value)
                                                                        }}</span>
                                                                </label>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="card mb-3 probx">
                                            <div class="card-header">
                                                <h2>Timeline</h2>
                                                <!-- <div class="col-md-6 text-right pr-0 bottom_filter_button pt-0">
                                                    <a href="javascript:;" id="ancActivityExpandAllCollapseAll"
                                                        class="position-relative" style="z-index:999;"
                                                        :title="$t('ExpandAllCollapseAll')"
                                                        data-toggle-tooltip="tooltip">
                                                        {{ $t('ExpandAllCollapseAll') }}
                                                    </a>
                                                </div> -->
                                            </div>
                                            <div class="col-md-12 mbspc" id="divconversationbox">
                                                <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tab-pane fade p-0 pb-2 border-0" id="tab-notes-content" role="tabpanel"
                            aria-labelledby="tab-notes-content" :class="{ 'active show': activeKey == 2 }">
                            <crmnote v-if="noteData" v-bind:notedata="noteData" :userid="userid" :companyid="companyId"
                                :sourceid="$route.params.id" :modulename="$route.params.moduleName"
                                :submodulecode="$route.params.subModuleCode" v-bind:listtype="NoteListType"
                                 :showNotePopUp="isShowNotePopup" callfrom="quickViewAndNoteJS">
                            </crmnote>
                        </div>

                        <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 3 }"
                            id="tab-attachment-content" role="tabpanel" aria-labelledby="tab-attachment-content">
                            <crmattachment v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id">
                            </crmattachment>
                        </div>
                        <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 4 }"
                            id="tab-priceBook-content" role="tabpanel" aria-labelledby="tab-priceBook-content">
                            <div class="table-responsive">
                                <table class="table table-bordered mb-0 border" id="table-price-content" aria-describedby="pricebookcontent">
                                    <thead class="thead-bg">
                                        <tr>
                                             <th scope="col" v-for="(item, index) in Headers" :key="index"><span
                                                    v-bind:id="item.COLUMN_NAME">{{ $t(item.COLUMN_NAME,
                                                            item.DISPLAY_NAME)
                                                    }}</span></th>
                                             <th scope="col"><span>{{ $t('Price') }}</span></th>
                                             <th scope="col" class="text-center"><span>{{ $t('Action') }}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in priceData" :key="index">
                                            <td v-for="(key, index) in Headers"
                                                v-bind:class="{ 'text-center': key.COLUMN_NAME == 'status_name' }"
                                                :key='index'>
                                                <span v-if="(key.DATA_TYPE == 'date' || key.DATA_TYPE == 'datetime')"
                                                    v-bind:class="{ 'px-2 bg-success text-white d-block text-center': (key.COLUMN_NAME == 'status_name' && item[key.COLUMN_NAME] == 'Active'), 'px-2 bg-danger text-white d-block text-center': (key.COLUMN_NAME == 'status_name' && item[key.COLUMN_NAME] == 'Inactive') }">
                                                    {{item[key.COLUMN_NAME] | formatDate}}
                                                    </span>
                                                <span v-else="(key.DATA_TYPE!='date' && key.DATA_TYPE=='datetime')"
                                                    v-bind:class="{ 'px-2 bg-success text-white d-block text-center': (key.COLUMN_NAME == 'status_name' && item[key.COLUMN_NAME] == 'Active'), 'px-2 bg-danger text-white d-block text-center': (key.COLUMN_NAME == 'status_name' && item[key.COLUMN_NAME] == 'Inactive') }">{{ item[key.COLUMN_NAME] }}</span>
                                            </td>                                           
                                            <td>{{isBlank(item.list_price)?"0.00":item.list_price | currency}}</td>
                                            <td class="text-center"><a href="javascript:;" v-on:click="deassociatePricebook(item['price_book_id'])"
                                            :title="$t('DeassociateProduct')"><em class="fa fa-times action-icon text-danger"></em></a></td>
                                        </tr>
                                        <tr v-if="noRecordForTab">
                                            <td class="text-center text-danger no-record" colspan="9">{{
                                                    $t("NoRecordfound")
                                            }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <modalattachment v-if="showAttachmentModal" @close="showAttachmentModal = false" :userid="userid"
            :companyid="companyId" :id="$route.params.id" :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" :containername="containerName" imagesizedata>
            <h5 slot="header" class="modal-title">{{ $t('Attachments') }}</h5>
        </modalattachment>
        <ModelPriceBook v-if="showPriceBookModal" @close="showPriceBookModal = false">
            <h5 slot="header" class="modal-title">{{ $t('PriceBook') }}</h5>
        </ModelPriceBook>
    </div>
</template>