<template>
   
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">{{$t('PriceBook')}}</h5>
                        </slot>
                        <div class="float-right">
                            
                        </div>
                        <button v-on:click="$emit('close')" type="button" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                    
                    <div class="col-md-12 p-0">
            <div class="theme-primary partition  p-actions-expand">
                
                <span class="p-actions float-right">
                                <!-- <a id="ancAddBreak" title="" checkprivilege="yes" hhref="javascript:;" class="p-action-btn text-white" data-toggle-tooltip="tooltip" data-original-title="Add a Break">
                                    <i class="fa fa-plus"></i><br>
                                    <span> Add</span>
                                </a> -->
                                <a id="addModelProduct" href="javascript:;" class="p-action-btn text-white float-right" title="Associate PriceBook" v-on:click="addBooks()">
                                    <em  class="fa fa-plus pr-2"></em> <br />{{$t('Associate')}} {{('PriceBook')}}</a>

                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                </span>
            </div>

        </div>

                    


                        <div class="listing mt-2">
                            <div id="divshowPricebookList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                <div class="form-group is-invalid" v-if="isPricebookSelected">
                                    <div class="alert alert-danger">
                                        <div class="pr-3 text-center" v-if="isPricebookSelected" style="display:block;">{{errorMessageForPricebook}}</div>
                                    </div>
                                </div>
                                <div class="table-responsive tableborder popuptablescroll_365">
                                    <table class="table-sorting table table-bordered m-0" v-if="!ModelnoRecord" aria-describedby="pricebikk">
                                        <thead class="thead-bg">
                                            <tr>
                                                 <th scope="col" style="width:50px !important;" class="text-center"><input id="chkAll" class="chkitems" value="" type="checkbox"></th>
                                                 <th scope="col" v-for="(item,index) in ModelHeaders" :key="index" ><span v-bind:class="{sort: item.SORTABLE, tb_headerSortUp :  SortExp=='ASC' && SortBy==item.COLUMN_NAME, tb_headerSortDown :  SortExp=='DESC' && SortBy==item.COLUMN_NAME}" v-on:click="sortdata(item.COLUMN_NAME)" v-bind:id="item.COLUMN_NAME">{{$t(item.COLUMN_NAME, item.DISPLAY_NAME)}}</span></th>
                                                 <th scope="col" class="text-center" style="min-width:150px;"><span class="d-inline-block">{{$t('PriceBook')}}<span class="text-danger d-inline-block">*</span></span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,index1) in ModelPricebookData" :key="index1">
                                                <td class="text-center"><input class="chkItems" v-bind:value="item.price_book_id" type="checkbox"></td>
                                                <td v-for="(key,index2) in ModelHeaders" :key="index2" v-bind:class="{'text-center':key.COLUMN_NAME=='status_name'}">
                                                    <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime')" v-bind:class="{'px-2 bg-success text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">{{ item[key.COLUMN_NAME] | formatDate}}</span>
                                                    <span v-else  v-bind:class="{'px-2 bg-success text-white d-block text-center': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME]}}</span>
                                                </td>
                                                <td><input class="form-control pricetxt" v-on:keypress="isNumber($event)" onpaste="return false;" maxlength="8" v-bind:id="'listPrice'+item.price_book_id" type="text" /> </td>
                                            </tr>
                                            <tr v-if="ModelnoRecord">
                                                <td class="text-center text-danger no-record" colspan="9">{{ $t("NoRecordfound") }}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <Pager v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="PageNumber" prev-text="Prev" 
                                first-text="First" last-text="Last" next-text="Next" @set-current-page="pagerMethod"></Pager>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
   
</template>

<script>
import Pager from '../../../components/Common/Pager.vue'
import ProductDataService from "../../../services/DataService";
export default {
    components: {
        Pager,
    },
    data(){
        return{
            ModelPricebookData: null,
                ModelHeaders: null,
                listPrice: null,
                ProductCheckBoxes: [],
                ModelnoRecord: false,
                isPricebookSelected: false,
                errorMessageForPricebook: '',
                errorMessageForPrice: '',
                TotalRecords: 0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                PageSizeOption: null,
                CurrentPage: 1,
                TotalPages: 0,
                GroupStart: 1,
                GroupEnd: 3,
                ProductName: '',
                ApiURL: `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`
        }
    },
    created:function(){
        var VueObj=this;
        VueObj.FetchData();
        VueObj.GetModelPageSizeOption();
    },
    methods:{
        isNumber: function (evt) {
          var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
        GetModelPageSizeOption: function () {
           var list = [];
           var defaultPageSize = 10;
           for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
               list.push(i);
           }
           this.PageSizeOption = list;
        },
        FetchData:function(){
            var VueObj=this;
            var params=VueObj.ApiURL;
            params+=`&pageSize=${this.PageSize}&pageNumber=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&productName=${this.ProductName}&statusId=1001&requestType=popup`
            ProductDataService.PriceBookListing(params).then(function (response) {
                if (response.data != null) {
                    VueObj.ModelPricebookData = response.data.data;
                    VueObj.ModelHeaders = response.data.schema;
                    VueObj.TotalRecords = response.data.data[0].totalrecords;
                    VueObj.TotalPages = Math.ceil(VueObj.TotalRecords / VueObj.PageSize);
                    VueObj.CurrentPage = VueObj.PageNumber;
                    VueObj.ModelnoRecord = false;
                }
                else {
                    VueObj.ModelnoRecord = true;
                    VueObj.TotalRecords = 0
                }
                setTimeout(function(){VueObj.CheckBoxBootstrap()}, 100);
            });
        },
        addBooks: function(){
            var VueObj=this;
            VueObj.isPricebookSelected = false;
            VueObj.errorMessageForPricebook = '';
            VueObj.errorMessageForPrice = '';
            var bookIds = [];
            var validateError = false;
            $("#divshowPricebookList table tr td input[type='text']").removeClass("is-invalid");
            $("#divshowPricebookList table tr td input[type='checkbox']:checked").each(function () {
                var valueObj = {
                    priceBookId: $(this).val(),
                    facePrice: $(this).closest('tr').find('input[type="text"]').val()
                }
                if ($.trim($(this).closest('tr').find('input[type="text"]').val()) <= 0 || $.trim($(this).closest('tr').find('input[type="text"]').val()) == '' || !(/([0-9]+(\.[0-9]+)?)/).test($.trim($(this).closest('tr').find('input[type="text"]').val()))) {
                    $(VueObj).closest('tr').find('input[type="text"]').addClass("is-invalid");
                    validateError = true;
                }
                bookIds.push(valueObj);
            });
            $("#divshowPricebookList table tr td input[type='checkbox']:checked").first().closest('tr').find('input[type="text"]').focus();
            if (bookIds.length > 0 && !validateError) {
                var bookMappingData = {
                    sourceId: VueObj.$route.params.id,
                    moduleName: VueObj.$route.params.moduleName,
                    subModuleCode: VueObj.$route.params.subModuleCode,
                    sourceName: 'pricebook_id',
                    pricebookdata: bookIds
                };
                var objectPostString = JSON.stringify(bookMappingData);
                ProductDataService.SavePriceBookForMapping(objectPostString).then(function (response) {
                    if (response.data[0].Status == "Success") {
                        VueObj.ShowAlert(VueObj.$t("Price book associated successfully."),"success", true, VueObj.$t("Alert"));
                        VueObj.$emit('close');     
                        VueObj.$parent.loadData('price');                   
                    }
                    
                });
            }
            else {
                VueObj.isPricebookSelected = true;
                VueObj.errorMessageForPricebook = 'Please check the item and fill respective price field';
            }
        },
        sortdata: function (s) {          
            var SortDir = "";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "" || this.SortExp == "DESC") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }                
                this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
            }
            
        },
        pagerMethod: function (value) {              
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },    
        SetCurrentPage: function (pageNumber) {
            this.PageNumber = pageNumber;
            if (pageNumber > this.GroupEnd || pageNumber == 1) {
                this.GroupStart = pageNumber;
                this.GroupEnd = pageNumber + 2;
            }
            else if (pageNumber < this.GroupStart && pageNumber > 1) {
                this.GroupEnd = this.GroupStart - 1;
                this.GroupStart = pageNumber - 2;
            }
            this.FetchData();
         }
    }
}
</script>
